button {
    background-color: rgb(184, 218, 176);
    width: 80px;
    height: 30px;
    margin-right: 10px;
    margin-left: 10px;
    border-radius: 3px;
    cursor: pointer;
}
li {
background-color: rgb(184, 218, 176);
height: auto;
width: 200px;
padding: 5px;
border: 1px solid black;
border-radius: 3px;
}

p {
    margin-left: 30px;
}
section {
    border: 1px solid red;
    margin-left: 30px;
    margin-top: 30px;
    width: 300px;
    background-color: rgb(200, 221, 228);
    border-radius: 5px;
}
h2 {
    
    margin-left: 25px;
}